<template>
  <!-- ![search box interface](https://i.loli.net/2019/04/10/5cad4362e8bef.png) -->
  <!-- 
    :beforeScroll="beforeScroll"
    @beforeScroll="listScroll" -->
    <!-- @scrollToEnd="searchMore"scroll( ) -->
  <scroll
    :pullup="pullup"
    class="suggest"
    ref="suggest"
    :data="result"
    @scrollToEnd="searchMore"
    @beforeScroll="listScroll"
    :beforeScroll="beforeScroll"
  >
    <ul class="suggest-list">
      <!-- v-for="(item,index) in result"
        :key="index"
         -->
      <li
        class="suggest-item"
        v-for="(item,index) in result"
        :key="index"
        @click="selectItem(item)"
      >
        <div class="icon">
          <!-- -->
          <i :class="getIconCls(item)"></i>
        </div>
        <div class="name">
          <!--  -->
          <p class="text" v-html="getDisplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore" title=""></loading>
    </ul>
    <!-- no-result -->
    <div class="no-result-wrapper" v-show="!hasMore && !result.length">
      <no-result title=""></no-result>
    </div>
  </scroll>
</template>

<script type="text/ecmascript-6">
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import Singer from 'common/js/singer'
import NoResult from 'base/no-result/no-result'
import { search } from 'api/search'
import { getMusic } from 'api/song'
import { ERR_OK } from 'api/config'
import { createSong } from 'common/js/song'
import { mapMutations, mapActions } from 'vuex'

const TYPE_SINGER = 'singer'
// 
const perpage = 20

export default {
  components: {
    Scroll,
    Loading,
    NoResult,
  //   Singer
  },
  props: {
    // 
    showSinger: {
      type: Boolean,
      default: true
    },
    // 
    query: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      // 
      page: 1,
      // 
      pullup: true, // drop-down refresh
      // beforeScroll: true,
      //    
      hasMore: true,
      // 
      result: [],
      flag: 1,
      beforeScroll: true,
    }
  },
  methods: {
    ...mapMutations({
      setSinger: 'SET_SINGER'
    }),
    ...mapActions(['insertSong']),
    // scroll
    refresh() {
      this.$refs.suggest.refresh()
    },
  // 
    search() { // request service-end data
    // querysearchpage
      this.page = 1
      this.hasMore = true
    // querysearch
      this.$refs.suggest.scrollTo(0, 0)
      search(this.query, this.page, this.showSinger, perpage).then(res => {
        if (res.code === ERR_OK) {
          // res.datasongzhida
          this.result = this._genResult(res.data)
          this._checkMore(res.data)
        }
      })
    },
    // 
    _genResult(data, res) {
      let ret = res || []
      // type: distinguish singer & song
      // zhidatype:singerret
      if (data.zhida && data.zhida.singerid && this.flag) {
        this.flag = 0;
        /* eslint-disable standard/object-curly-even-spacing */
        // data.zhidatype:'singer'type:'singer'
        ret.push({ ...data.zhida, ...{ type: TYPE_SINGER }})
      }
      // ret
      if (data.song) {
        // 
        ret = this._normalizeSongs(ret, data.song.list);
      }
      return ret;
    },
    // _normalizeSongs(list) { // filter
    //   const ret = []
    //   list.forEach(musicData => {
    //     if (musicData.songid && musicData.albummid) {
    //       ret.push(createSong(musicData))
    //     }
    //   })
    //   return ret
    // },
   _normalizeSongs(ret, list) {
    //  let ret = ret;
     list.forEach((item) => {
    //  let {musicData} = item
     if (item.songid && item.albummid) {
          getMusic(item.songmid).then((res) => { // vkey
     if (res.code === ERR_OK) {
     const svkey = res.data.items
     const songVkey = svkey[0].vkey
     const newSong = createSong(item, songVkey) // vkeymusicData
     ret.push(newSong)
     }
     })
     }
     })
     return ret
    },
    // 
    searchMore() {
      // 
      if (!this.hasMore) {
        return
      }
      // page++,page
      this.page++
      search(this.query, this.page, this.showSinger, perpage).then(res => {
        if (res.code === ERR_OK) {
          this.result = this._genResult(res.data, this.result);
          this._checkMore(res.data)
        }
      })
    },
    _checkMore(data) {
      const song = data.song
      // datasong
      // 1. curnum
      // 2. curpage
      // 3. 

      // 0
      
      if (!song.list.length || song.curnum + (song.curpage) * perpage > song.totalnum) {
        this.hasMore = false
      }
    },
    listScroll() {
      this.$emit('listScroll')
    },
  // 
    selectItem(item) {
      // 
      if (item.type === TYPE_SINGER) {
        // 
        const singer = new Singer({
          id: item.singermid,
          name: item.singername
        })
        // 
        this.$router.push({
          path: `/search/${singer.id}`
        })
        this.setSinger(singer)
      } else {
        // 
        this.insertSong(item)
      }
      // 
      this.$emit('select', item)
    },
  // 
    getDisplayName(item) {
      if (item.type === TYPE_SINGER) {
        // 
        return item.singername
      } else {
        // 
        // itemsongitem.name,item.songname
        return `${item.name}-${item.singer}`
      }
    },
  // 
    getIconCls(item) {
      if (item.type === TYPE_SINGER) {
        return 'icon-mine'
      } else {
        return 'icon-music'
      }
    }
  },
  watch: {
    // query
    query(newQuery) { // request server **api/search.js**
      this.search(newQuery)
    }
  }
}
</script>

<style scoped lang="scss" rel="stylesheet/scss">
@import './suggest.scss';
</style>



