<template>
  <!-- ![search box interface](https://i.loli.net/2019/04/10/5cad4362e8bef.png) -->
  <div class="search">
    <div class="search-box-wrapper">
       <!-- @query="onQueryChange" -->
      <search-box ref="searchBox" @query="onQueryChange"></search-box>
    </div>
      <!-- v-show="!query" -->
    <div
      class="shortcut-wrapper"
      ref="shortcutWrapper"
       v-show="!query"
       :refreshDelay="refreshDelay"
    >
      <scroll
        class="shortcut"
        ref="shortcut"
        :data="shortcut"
      >
        <div>
          <div class="hot-key">
            <h1 class="title"></h1>
            <ul>
              <!--hotkey:qq  -->
              <!-- li -->
              <li
                class="item"
                v-for="(item,index) in hotKey"
                :key="index"
                @click="addQuery(item.k)"
              >
                <span>{{item.k}}</span>
              </li>
            </ul>
          </div>
          <div class="search-history" v-show="searchHistory.length">
            <h1 class="title">
              <span class="text"></span>
              <!-- @click="showConfirm" -->
              <span class="clear" @click="showConfirm">
                <i class="icon-clear"></i>
              </span>
            </h1>
            <!--  -->
            <!-- @delete="deleteSearchHistory"
               -->
            <search-list
              :searches="searchHistory"
              @select="addQuery"
              @delete="deleteOne"
            >
            </search-list>
          </div> 
        </div>
      </scroll>
    </div>
    <div
      class="search-result"
      v-show="query"
      ref="searchResult"
    >
      <!--v-show="query"  -->
      <suggest
        ref="suggest"
        :query="query"
        v-show="query"
        @listScroll="blurInput"
        @select="saveSearch"
      >
      </suggest>
    </div>
    <confirm
      ref="confirm"
      text=""
      confirmBtnText=""
      @confirm="clearSearchHistory"
    >
    </confirm>
    <!--  -->
    <router-view></router-view> 
  </div>
</template>

<script type="text/ecmascript-6">
// mapGetters mixin
import { mapActions } from 'vuex'
import { getHotKey } from 'api/search'
import { ERR_OK } from 'api/config'
import { playlistMixin, searchMixin } from 'common/js/mixin'
import Scroll from 'base/scroll/scroll'
import SearchBox from 'base/search-box/search-box'
import Suggest from 'components/suggest/suggest'
import SearchList from 'base/search-list/search-list'
import Confirm from 'base/confirm/confirm'

export default {
  // mixins: [playlistMixin, searchMixin],
  mixins: [playlistMixin, searchMixin],
  components: {
    SearchBox,
    Scroll,
    Suggest,
    SearchList,
    Confirm
  },
  data() { 
    return {
      hotKey: [],
      // mixin
      // query: ''
    }
  },
  created() {
    this._getHotKey()
  },
  computed: {
    // mixin
    // ...mapGetters(['searchHistory']),
    // scroll
    shortcut() { // when hotKey | searchHistory changes, scroll reset height
      return this.hotKey.concat(this.searchHistory)
    }
  },
  methods: {
    // mixin
    // onQueryChange(query){
    //   this.query = query;
    // },
    // 'saveSearchHistory', 'deleteSearchHistory'mixin
    ...mapActions(['clearSearchHistory']),
    handlePlaylist(playlist) {
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.searchResult.style.bottom = bottom
      // suggestrefresh,scroll
      this.$refs.suggest.refresh()
      this.$refs.shortcutWrapper.style.bottom = bottom
      this.$refs.shortcut.refresh()
    },
  // 
    showConfirm() {
      this.$refs.confirm.show()
    },
    // mixin 
  // // 
  //   blurInput(){
  //     this.$refs.searchBox.blur();
  //   },
  //   // 
  //   saveSearch(){
  //     this.saveSearchHistory(this.query);
  //   },
    // 
    deleteOne(item){
      this.deleteSearchHistory(item);
    },
    // deleteAll(){
    //   this.clearSearchHistory();
    // },
    // 
    addQuery(query) {
      // 
      this.$refs.searchBox.setQuery(query)
    },
    // 
    _getHotKey() {
      getHotKey().then(res => {
        if (res.code === ERR_OK) {
          // 10hotkey
          this.hotKey = res.data.hotkey.slice(0, 10)
        }
      })
    }
  },
  watch: {
    query(newQuery) { // Prevent current interface from disable scrolle
      if (!newQuery) {
        setTimeout(() => {
          this.$refs.shortcut.refresh()
        }, 20)
      }
    }
  }
}
</script>

<style scoped lang="scss" rel="stylesheet/scss">
@import './search.scss';
</style>



