<template>
  <!-- ![singer detail interface](https://i.loli.net/2019/04/08/5caac3e8e7a0f.png) -->
  <div class="music-list">
    <div class="back" @click="back">
      <i class="icon-back"></i>
    </div>
    <!-- v-html="title": titletitle -->
    <h1 class="title" v-html="title"></h1>
    <div class="bg-image" ref="bgImage" :style="bgStyle">
      <!-- <div class="filter"></div> -->
      <div class="play-wrapper">
        <!-- v-show="songs.length>0" -->
        <div
          class="play"
          ref="playBtn"
          v-show="songs.length>0"
          @click="random"
        >
          <i class="icon-play"></i>
          <span class="text"></span>
        </div>
      </div>
      <div class="filter" ref="filter"></div>
    </div>
    <div class="bg-layer" ref="layer"></div>
    <!-- songsscroll -->
    <scroll :data="songs" class="list" ref="list" :listen-scroll="listenScroll" :probe-type="probeType" @scroll="scroll">
      <!-- <div class="song-list-wrapper"> -->
        <song-list :songs="songs" @select="selectItem" :rank="rank">
        </song-list>
      <!-- </div> -->
      <div v-show="!songs.length" class="loading-container">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script type="text/ecmascript-6">
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import SongList from 'base/song-list/song-list'

// 
import { prefixStyle } from 'common/js/dom'
import { playlistMixin } from 'common/js/mixin'
import { mapActions } from 'vuex'

const RESERVED_HEIGHT = 40
// css
const transform = prefixStyle('transform')
const backdrop = prefixStyle('backdrop-filter')

export default {
  mixins: [playlistMixin],
  components: {
    Scroll,
    Loading,
    SongList
  },
  props: {
    bgImage: {
      type: String,
      default: ''
    },
    songs: {
      type: Array,
      default: null
    },
    title: {
      type: String,
      default: ''
    },
    // 
    rank: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scrollY: 0 // real time roll position
    }
  },
  created() {
    this.probeType = 3
    this.listenScroll = true
  },
  mounted() {
    this.imageHeight = this.$refs.bgImage.clientHeight
    this.minTransalteY = -this.imageHeight + RESERVED_HEIGHT // bg-layer minTransalteY

  //  this.$refs.listscrollvue-components$eldom,song-listpositiontop
    // this.$refs.list.$el.style.top = `${this.imageHeight}px`
    this.$refs.list.$el.style.top = `${this.$refs.bgImage.clientHeight}px`
  },
  computed: { 
    bgStyle() {
      return `background-image:url(${this.bgImage})`
    }
  },
  methods: {
    ...mapActions(['selectPlay', 'randomPlay']),
    handlePlaylist(playlist) {
      // playlistbottom60px
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.list.$el.style.bottom = bottom
      // console.log(this.$refs.list, '-----------')
      this.$refs.list.refresh()
    },
    scroll(pos) {
      // 
      // pos
      // 
      // pos0
      this.scrollY = pos.y
    },
    // 
    back() {
      this.$router.back()
    },
    selectItem(item, index) {
      this.selectPlay({
        list: this.songs,
        index
      })
      // console.log(this.currentIndex);
    },
    random() {
      this.randomPlay({
        list: this.songs
      })
    }
  },
  watch: {
    scrollY(newVal) {
      const translateY = Math.max(this.minTransalteY, newVal)
      // 
      let scale = 1
      // 
      let zIndex = 0
      // 
      let blur = 0
      const percent = Math.abs(newVal / this.imageHeight)
      if (newVal > 0) { // scroll down
        scale = 1 + percent
        // zIndex = 10
        zIndex = 10
      } else {
        blur = Math.min(percent * 20, 20)
      }
  // 
      this.$refs.layer.style[transform] = `translate3d(0,${translateY}px,0)`
      // this.$refs.layer.style['webkitTransform'] = `translate3d(0,${translateY}px,0)`
      // css
      this.$refs.filter.style[backdrop] = `blur(${blur}px)` // gaussian-blur: iphone view
      if (newVal < this.minTransalteY) { // scroll to top
        zIndex = 10
        // widthpadding-top10:7heightpaddingTop0
        this.$refs.bgImage.style.paddingTop = 0
        this.$refs.bgImage.style.height = `${RESERVED_HEIGHT}px`
        // 
        this.$refs.playBtn.style.display = 'none'
      } else {
        this.$refs.bgImage.style.paddingTop = '70%'
        this.$refs.bgImage.style.height = 0
        // display = ''
        this.$refs.playBtn.style.display = ''
      }
      // 
      this.$refs.bgImage.style[transform] = `scale(${scale})`      
      // this.$refs.bgImage.style[webkitTransform] = `scale(${scale})`
      // ()
      this.$refs.bgImage.style.zIndex = zIndex
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "./music-list.scss";
</style>



