<template>
  <!-- ![user center interface](https://i.loli.net/2019/04/10/5cadb1c58b527.png) -->
  <transition name="slide">
    <div class="user-center">
       <!-- .back -->
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <!-- .switches-wrapper:/ -->
      <div class="switches-wrapper">
        <switches
          :currentIndex="currentIndex"
          :switches="switches"
          @switch="switchItem"
        >
        </switches>
      </div>
      <div
        class="play-btn"
        ref="playBtn"
        @click="random"
      >
        <i class="icon-play"></i>
        <span class="text"></span>
      </div>
      <!-- .list-wrapper: -->
      <div class="list-wrapper" ref="listWrapper">
        <!-- ref="favoriteList"-->
        <scroll
          class="list-scroll"
          ref="favoriteList"
          v-if="currentIndex===0"
          :data="favoriteList"
        >
          <div class="list-inner">
            <song-list :songs="favoriteList" @select="selectSong"></song-list>
          </div>
        </scroll>
        <!--ref="playList"  -->
        <scroll
          class="list-scroll"
          ref="playList"
          v-if="currentIndex===1"
          :data="playHistory"
        >
          <div class="list-inner">
            <song-list :songs="playHistory" @select="selectSong"></song-list>
          </div>
        </scroll>
        
      </div>
      <!-- /  -->
      <div class="no-result-wrapper" v-show="noResult">
        <!-- noResult -->
        <!-- noResultDesc -->
        <no-result :title="noResultDesc"></no-result>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import { mapGetters, mapActions } from 'vuex'
// scroll
import { playlistMixin } from 'common/js/mixin'
import Switches from 'base/switches/switches'
import Scroll from 'base/scroll/scroll'
import SongList from 'base/song-list/song-list'
import NoResult from 'base/no-result/no-result'
import Song from 'common/js/song'

export default {
  components: {
    Switches,
    Scroll,
    SongList,
    NoResult
  },
  mixins: [playlistMixin],
  data() {
    return {
      currentIndex: 0,
      switches: [{ name: '' }, { name: '' }]
    }
  },
  computed: {
    ...mapGetters([
      // 
      'favoriteList',
      'playHistory'
    ]),
    // /  
    noResult() {
      if (this.currentIndex === 0) {
        return !this.favoriteList.length
      } else {
        return !this.playHistory.length
      }
    },
    noResultDesc() {
      if (this.currentIndex === 0) {
        return ''
      } else {
        return ''
      }
    }
  },
  methods: {
    ...mapActions([
      'insertSong',
      'randomPlay'
    ]),
    // scroll
    handlePlaylist(playlist) {
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.listWrapper.style.bottom = bottom
      // favoriteList
      this.$refs.favoriteList && this.$refs.favoriteList.refresh() // $refs.favoriteList use v-if, make sure it is not undefined
      this.$refs.playList && this.$refs.playList.refresh()
    },
    switchItem(index) {
      this.currentIndex = index
    },
    selectSong(song) {
      // 
      this.insertSong(new Song(song))
    },
    back() {
      this.$router.back()
    },
    // 
    random() {
      let list = this.currentIndex === 0 ? this.favoriteList : this.playHistory
      // 
      if (list.length === 0) {
        return
      }
      // listsong
      list = list.map(song => {
        return new Song(song)
      })
      this.randomPlay({ list })
    }
  }
}
</script>

<style scoped lang="scss" rel="stylesheet/scss">
@import './user-center.scss';
</style>



