<template>
  <!-- ![add song to list interface](https://i.loli.net/2019/04/10/5cada57184b01.png) -->
  <transition name="slide">
     <!--  @click.stopplaylistplaylist -->
    <div class="add-song" v-show="showFlag" @click.stop>
    <!--.headerheader -->
      <div class="header">
        <h1 class="title"></h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <!-- .search-box-wrapper -->
      <div class="search-box-wrapper">
        <!-- @query="onQueryChange" -->
        <search-box
          ref="searchBox"
          placeholder=""
          @query="onQueryChange"
        >
        </search-box>
      </div>
       <!-- -->
       <!-- .shortcut -->
      <div class="shortcut"  v-show="!query">
        <!-- @switch="switchItem" -->
        <!--switches -->
        <switches
          :currentIndex="currentIndex"
          :switches="switches"
          @switch="switchItem"
        >
        </switches>
        <div class="list-wrapper">
          <!--ref=songList: -->
          <scroll
            class="list-scroll"
            ref="songList"
            v-if="currentIndex === 0"
            :data="playHistory"
          >
            <div class="list-inner">
              <!--  -->
              <!-- @select="selectSong"selectsong-list -->
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>
          <!-- ref="searchList": -->
          <scroll
            class="list-scroll"
            ref="searchList"
            v-if="currentIndex === 1"
            :data="searchHistory"
            :refreshDelay="refreshDelay"
          >
          <!-- :data="searchHistory":mixinvue 
                :searches="searchHistory"
                @delete="deleteSearchHistory"
                @select="addQuery"
                mixin 
          -->
            <div class="list-inner">
              <search-list
                :searches="searchHistory"
                @delete="deleteSearchHistory"
                @select="addQuery"
              >
              </search-list>
            </div>
          </scroll>
        </div>
      </div>
      <!-- .search-result -->
      <!-- v-show="query" -->
      <div class="search-result" v-show="query">
        <suggest
          :query="query"
          :showSinger="showSinger"
          @listScroll="blurInput"
          @select="selectSuggest"
        >
        </suggest>
      </div>
      <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
// 
import SearchBox from 'base/search-box/search-box'
import SongList from 'base/song-list/song-list'
import SearchList from 'base/search-list/search-list'
import Scroll from 'base/scroll/scroll'
import Switches from 'base/switches/switches'
import TopTip from 'base/top-tip/top-tip'
// 
import Suggest from 'components/suggest/suggest'
import { searchMixin } from 'common/js/mixin'
import { mapGetters, mapActions } from 'vuex'
//  Song
import Song from 'common/js/song'

export default {
  components: {
    // 
    SearchBox,
    SearchList,
    // 
    SongList,
//     SearchList,
    Scroll,
    // 
    Switches,
    // 
    TopTip,
// 
    Suggest
  },
  mixins: [searchMixin],
  data() {
    return {
      // mixin
      // query: '',
      // 
      showFlag: false,
      // 
      showSinger: false,
//       songs: [],
      // 
      currentIndex: 0,
      switches: [{ name: '' }, { name: '' }]
    }
  },
  computed: {
    ...mapGetters(['playHistory'])
  },
  methods: {
    ...mapActions(['insertSong']),
    show() {
      this.showFlag = true
      setTimeout(() => {
        // scroll
        if (this.currentIndex === 0) {
          this.$refs.songList.refresh()
        } else {
          this.$refs.searchList.refresh()
        }
      }, 20)
    },
    hide() {
      this.showFlag = false
    },
    // search(query){
    //   this.query = query;
    // },
    // 
    selectSong(song, index) {
      if (index !== 0) { 
        // songSongSong
        this.insertSong(new Song(song)) // new Song() -> , song 
        this.$refs.topTip.show()
      }
    },
    selectSuggest() {
      this.$refs.topTip.show()
      // mixin
      this.saveSearch()
    },
    // currentIndex(/)
    switchItem(index) {
      this.currentIndex = index
    }
  }
}
</script>

<style scoped lang="scss" rel="stylesheet/scss">
@import './add-song.scss';
</style>



