<template>
  <div class="singer" ref="singer">
    <!-- @select="selectSinger" -->
    <list-view :data="singers" ref="list" @select="selectSinger"></list-view>
    <router-view></router-view>
  </div>
</template>
<script>
import { getSingerList } from 'api/singer'
import { ERR_OK } from 'api/config'
import Singer from 'common/js/singer'
import { playlistMixin } from 'common/js/mixin'
import ListView from 'base/listview/listview'
import {mapMutations} from 'vuex'
// 10
const HOT_SINGER_LEN = 10
// title
const HOT_NAME = ''
export default {
  mixins: [playlistMixin],
  data() {
    return {
      singers: []
    }
  },
  components:{
    ListView,
  },
  created() {
    this._getSingerList() // get singer data
  },
  methods:{
    handlePlaylist(playlist) {
      // playlistbottom60px
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.singer.style.bottom = bottom
      // console.log(this.$refs.list, '-----------')
      this.$refs.list.refresh()
    },
     _getSingerList() {
      getSingerList().then(res => {
        if (res.code === ERR_OK) {
          // [singer data](https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg?g_tk=5381&inCharset=utf-8&outCharset=utf-8&notice=0&format=jsonp&hostUin=0&needNewCode=0&platform=yqq&order=listen&begin=0&num=80&songstatus=1&singermid=0025NhlN2yWrP4&jsonpCallback=__jp1)
          // this.singers = this._normalizeSinger(res.data.list)
          this.singers = this._normalizeSinger(res.data.list);
        }
      })
    },
    // 
    _normalizeSinger(list) {
      // titleitems
      const map = {
        hot: {
          title: HOT_NAME,
          items: []
        }
      }
      // map10
      list.forEach((item, index) => {
        if (index < HOT_SINGER_LEN) { // previous 10 data, include ''
          // map.hot.items.push(
          //   {
          //     name: item.Fsinger_name,
          //     id: item.Fsinger_mid,
          //     avtor: `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.Fsinger_mid}.jpg?max_age=2592000`,
          //   }
          // )
          map.hot.items.push(
            new Singer({
              name: item.Fsinger_name,
              id: item.Fsinger_mid
            })
          )
        }
        // FindexFindex
        const key = item.Findex
        if (!map[key]) { // after 10 data, not include ''
          map[key] = {
            title: key,
            items: []
          }
        }
        // map[key].items.push(
        //     {
        //       name: item.Fsinger_name,
        //       id: item.Fsinger_mid,
        //       avtor: `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.Fsinger_mid}.jpg?max_age=2592000`,
        //     }
        //   )
        map[key].items.push(
          new Singer({
            name: item.Fsinger_name,
            id: item.Fsinger_mid
          })
        )
      })

      // map
      // console.log(map);
      // map

      // 
      const ret = []
      // 
      const hot = []
      for (const key in map) {
        const val = map[key]
        // key
        if (val.title.match(/[a-zA-Z]/)) { // distinguishes '' & letter
          ret.push(val)
          // 
        } else if (val.title === HOT_NAME) {
          hot.push(val)
        }
      }

      // 
      ret.sort((a, b) => { // ascend sort
        return a.title.charCodeAt(0) - b.title.charCodeAt(0)
      })
      // 
      return hot.concat(ret)
    },
    selectSinger(singer) {
      this.$router.push({ // route jump
        path: `/singer/${singer.id}`
      })
      this.setSinger(singer)
    },
    // this.setSinger
    ...mapMutations({
      setSinger: 'SET_SINGER'
    })
  }
}
</script>
<style scoped lang="scss" rel="stylesheet/scss">
// .singer.scss.singerposition: fixed;
// better-scroll
@import './singer.scss';
</style>


