<template>
  <!-- ![music player interface](https://i.loli.net/2019/04/09/5cac16b7d5a91.png) -->
  <!-- 
  normal-player:
  mini-player
  -->
  <div class="player" v-show="playlist.length>0">
    <!-- name="normal"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
      @after-leave="afterLeave" -->
    <transition name="normal"  
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
      @after-leave="afterLeave">
      <div class="normal-player" v-show="fullScreen">
        <!-- 1. -->
        <div class="background">
          <img width="100%" height="100%" :src="currentSong.image"/>
        </div>
        <!-- 2.1 -->
        <div class="top">
          <!-- 2.1.1 -->
          <div class="back" @click="back">
            <i class="icon-back"></i>
          </div>
          <!-- 2.1.2 -->
          <h1 class="title" v-html="currentSong.name"></h1>
          <!-- 2.1.3 s-->
          <h2 class="subtitle" v-html="currentSong.singer"></h2>
        </div>
        <!-- 2.2 -->
        <!-- CD -->
        <div class="middle" 
          @touchstart.prevent="middleTouchStart"
          @touchmove.prevent="middleTouchMove"
          @touchend="middleTouchEnd">
          <div class="middle-l" ref="middleL">
            <div class="cd-wrapper" ref="cdWrapper">
              <!-- 2.2.1 -->
              <div class="cd" :class="cdCls">
                <img class="image" :src="currentSong.image"/>
              </div>
            </div>
            <!-- cd -->
            <div class="playing-lyric-wrapper">
              <div class="playing-lyric">{{playingLyric}}</div>
            </div>
          </div>
          <!-- :data="currentLyric && currentLyric.lines": currentLyric!==null -->
          <!-- scrollrefresh -->
          <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
            <!--  -->
            <div class="lyric-wrapper">
              <div v-if="currentLyric">
                <p
                  class="text"
                  ref="lyricLine"
                  v-for="(line,index) in currentLyric.lines"
                  :key="index"
                  :class="{'current': currentLineNum === index}"
                >
                <!-- index -->
                  {{line.txt}}
                </p>
              </div>
            </div>
          </scroll>
        </div>
        <!-- 2.3 -->
        <div class="bottom">
          <!--dot-wrapper: , -->
          <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow==='cd'}"></span>
            <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
          </div>
          <div class="progress-wrapper">
            <!--  -->
            <span class="time time-l">{{format(currentTime)}}</span>
            <div class="progress-bar-wrapper">
              <!--  -->
              <!-- onProgressBarChangepercentChange -->
              <progress-bar :percent="percent" @percentChange="onProgressBarChange"></progress-bar>
            </div>
            <!--  -->
            <span class="time time-r">{{format(currentSong.duration)}}</span>
          </div> 
          <div class="operators">
            <div class="icon i-left" @click="changeMode">
              <i :class="iconMode"></i>
            </div>
            <div class="icon i-left" :class="disableCls">
              <i @click="prev" class="icon-prev" ></i>
            </div>
            <!--  -->
            <div class="icon i-center" :class="disableCls">
              <i :class="playIcon" @click="togglePlaying"></i>
            </div>
            <div class="icon i-right" :class="disableCls">
              <i @click="next" class="icon-next"></i>
            </div>
            <!--  -->
            <div class="icon i-right">
              <!-- 
                getFavoriteIcon
                toggleFavorite
                mixinplaylist
                -->
              <i class="icon" :class="getFavoriteIcon(currentSong)" @click="toggleFavorite(currentSong)"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <!--  -->
    <transition name="mini">
      <div class="mini-player" v-show="!fullScreen" @click="open">
        <!-- 1. -->
        <div class="icon">
          <img width="40" height="40" :src="currentSong.image" :class="cdCls"/>
        </div>
        <div class="text">
          <!-- 2. -->
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc" v-html="currentSong.singer"></p>
        </div>
        <!-- 3. -->
        <div class="control">
          <!-- insert slot -->
          <progress-circle :radius="radius" :percent="percent">
            <i class="icon-mini" :class="miniIcon" @click.stop="togglePlaying"></i>
          </progress-circle>
        </div>
        <!-- 4.  ,() --> 
        <div class="control" @click.stop="showPlaylist">
          <i class="icon-playlist"></i>
        </div>
      </div>
    </transition>
    <playlist ref="playlist"></playlist>

    <!--  -->
    <!-- audio
    play
    error
    timeupdate="updateTime":
    ended="end": 
     -->
    <audio
      ref="audio"
      :src="currentSong.url"
      @play="ready"
      @error="error"
      @timeupdate="updateTime"
      @ended="end"
    >
    <!-- currentSongplay -->
    </audio>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapGetters, mapMutations, mapActions } from 'vuex'
// `
import { prefixStyle } from 'common/js/dom'
import { playMode } from 'common/js/config'
import { shuffle } from 'common/js/util'
import { playerMixin } from 'common/js/mixin'
import animations from 'create-keyframe-animation'
// 
import ProgressBar from 'base/progress-bar/progress-bar'
import ProgressCircle from 'base/progress-circle/progress-circle'
import Lyric from 'lyric-parser'
import Scroll from 'base/scroll/scroll'
import Playlist from 'components/playlist/playlist'

const transform = prefixStyle('transform')
const transitionDuration = prefixStyle('transitionDuration')

export default {
  components: {
    ProgressBar,
    ProgressCircle,
    Scroll,
    Playlist
  },
  mixins: [playerMixin],
  data() {
    return {
      // 
      songReady: false,
      // audio
      currentTime: 0,
      // svg
      radius: 32,
      // 
      currentLyric: null,
      // 
      currentLineNum: 0, // lyrics highlight
      // cd
      currentShow: 'cd', // disc interface and lyrics interface
      // cd
      playingLyric: '' // CD below lyrics
    }
  },
  created() {
    // touchtouchdowntouchmovetouchgettersetter
    this.touch = {}
  },
  computed: {
  // 'fullScreen':v-show
    ...mapGetters([
      // 
      'currentIndex', 
      // 
      'fullScreen', 
      // 'playing',
      // playlistlength>0
      'playlist',
      // 
      'currentSong',
      // playinggettersvuexplayingfalsemusic-list.vueactionsstateplaying
      'playing',
      // : 
      'mode',
      // 
      'sequenceList',
      ]),
    cdCls() {
      return this.playing ? 'play' : 'play pause'
    },
    playIcon() {
      return this.playing ? 'icon-pause' : 'icon-play'
    },
    // iconModemixin
    // iconMode(){
    //   return this.mode === playMode.sequence? 'icon-sequence' : this.mode === playMode.loop? 'icon-loop' : 'icon-random'
    // },
    miniIcon() {
      return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
    },
    disableCls() {
      return this.songReady ? '' : 'disable'
    },
    // 
    percent() {
      return this.currentTime / this.currentSong.duration
    }
  },
  methods: {
    ...mapMutations({
      // statefullScreenmutations
      setFullScreen: 'SET_FULL_SCREEN',
      // stateplayingmutations
      setPlayingState: 'SET_PLAYING_STATE',
      setCurrentIndex:'SET_CURRENT_INDEX',
      setPlayMode: 'SET_PLAY_MODE',
      setPlayList:'SET_PLAYLIST',
    }),
    ...mapActions(['savePlayHistory']),
    back() {
      // this.fullScreen = falseVuex
      this.setFullScreen(false)
    },
    open() {
      this.setFullScreen(true)
    },
    // doneafterenter
    // CDCD
    enter(el, done) {
      const { x, y, scale } = this._getPosAndScale()
      const animation = {
        0: {
          transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
        },
        60: {
          transform: `translate3d(0,0,0) scale(1.1)`
        },
        100: {
          transform: `translate3d(0,0,0) scale(1)`
        }
      }
      // 
      animations.registerAnimation({
        name: 'move',
        animation,
        presets: {
          duration: 400,
          easing: 'linear'
        }
      })
      // domdoneafterenter
      animations.runAnimation(this.$refs.cdWrapper, 'move', done)
    },
    afterEnter() {
      // 
      animations.unregisterAnimation('move')
      this.$refs.cdWrapper.style.animation = ''
    },
    // doneafterleave
    // leaveCDCD
    leave(el, done) {
      const { x, y, scale } = this._getPosAndScale()
      this.$refs.cdWrapper.style.transition = 'all 0.4s'
      this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
      // afterleave
      this.$refs.cdWrapper.addEventListener('transitionend', done)
    },
    afterLeave() {
      this.$refs.cdWrapper.style.transition = ''
      this.$refs.cdWrapper.style[transform] = ''
    },
    togglePlaying() {
      // if (!this.songReady) {
      //   return
      // }
      this.setPlayingState(!this.playing)
      if (this.currentLyric) { // when scroll lyrics can play, preventing the lyrics from scrolling while they stop playing
      // 
        this.currentLyric.togglePlay()
      }
    },
    // mixin
    // changeMode(){
    //   const mode = (this.mode + 1) % 3
    //   this.setPlayMode(mode)
    //   let list = null
    //   if(mode === playMode.random){
    //     list = shuffle(this.sequenceList)
    //   }
    //   else{
    //     list = this.sequenceList
    //   }
    //   this.resetCurrentIndex(list);
    //   this.setPlayList(list)
    // },
    // resetCurrentIndex(list){
    //   let index = list.findIndex((item) => {
    //     return item.id === this.currentSong.id
    //   })
    //   this.setCurrentIndex(index);
    // },
    // 
    end() {
      if (this.mode === playMode.loop) {
        // loop
        this.loop()
      } else {
        // ()
        this.next()
      }
    },
    loop() {
      this.$refs.audio.currentTime = 0
      this.$refs.audio.play()
      this.setPlayingState(true)
      if (this.currentLyric) {
        // 
        this.currentLyric.seek(0) // <audio>, song jump to begin
      }
    },
    next() {
      // 
      if (!this.songReady) {
        return
      }
      if (this.playlist.length === 1) { // only one song
        this.loop()
        return
      } else {
        let index = this.currentIndex + 1
        // 
        if (index === this.playlist.length) {
          index = 0
        }
        // mutationsstate
        this.setCurrentIndex(index)
        // ()
        if (!this.playing) {
          this.togglePlaying()
        }
      }
      // this.songReady = false, 
      this.songReady = false
    },
    prev() {
      if (!this.songReady) {
        return
      }
      if (this.playlist.length === 1) {
        this.loop()
        return
      } else {
        let index = this.currentIndex - 1
        // 
        if (index === -1) {
          index = this.playlist.length - 1
        }
        this.setCurrentIndex(index)
        // ()
        if (!this.playing) {
          this.togglePlaying()
        }
      }
      this.songReady = false
    },
    // 
    ready() {
      this.songReady = true  
      // 
      this.savePlayHistory(this.currentSong)
    },
    error() {
      // :
      this.songReady = true
    },
    updateTime(e) {
      // updateetargetaudio
      // audiocurrenTime,
      // currentTime
      this.currentTime = e.target.currentTime // <audio> current time
    },
    // 
    format(interval) {
      //interval|0
      interval = interval | 0  // | 0: math.floor
      const minute = (interval / 60) | 0
      // 0.060.60pad
      const second = this._pad(interval % 60) // _pad: Use 0 to fill 2 bits
      return `${minute}:${second}`
    },
    // 0
    _pad(num, n = 2) { // _pad: use 0 to fill 2 bits
    // 
      let len = num.toString().length
      while (len < n) {
        num = '0' + num
        len++
      }
      return num
    },
    onProgressBarChange(percent) {
      const currentTime = this.currentSong.duration * percent
      // audio.currentTime
      this.$refs.audio.currentTime = currentTime
      // 
      if (!this.playing) {
        this.togglePlaying()
      }
      // 
      if (this.currentLyric) {
        // currentTime
        this.currentLyric.seek(currentTime * 1000)
      }
    },
    getLyric() {
      this.currentSong.getLyric().then(lyric => {
          if (this.currentSong.lyric !== lyric) { // prevent fast switch, result in unmatched lyric
            return
          }
          // this.handleLyric
          this.currentLyric = new Lyric(lyric, this.handleLyric)
          if (this.playing) {
            this.currentLyric.play()
          }
        })
        // 
        .catch(() => {
          this.currentLyric = null
          this.playingLyric = ''
          this.currentLineNum = 0
        })
    },
    // handleLyric
    // lineNum
    // txt
    handleLyric({ lineNum, txt }) {
      this.currentLineNum = lineNum
      if (lineNum > 5) {
        const lineEl = this.$refs.lyricLine[lineNum - 5]
        // 1
        this.$refs.lyricList.scrollToElement(lineEl, 1000)
      } else {
        // 1
        this.$refs.lyricList.scrollTo(0, 0, 1000)
      }
      // cd
      this.playingLyric = txt
    },
    showPlaylist() {
      this.$refs.playlist.show()
    },
    middleTouchStart(e) {
      this.touch.initiated = true
      this.touch.moved = false
      const touch = e.touches[0] // finger click info
      // 
      this.touch.startX = touch.pageX
      this.touch.startY = touch.pageY
    },
    middleTouchMove(e) {
      if (!this.touch.initiated) {
        return
      }
      const touch = e.touches[0]
      // 
      const deltaX = touch.pageX - this.touch.startX
      const deltaY = touch.pageY - this.touch.startY
      // 
      if (Math.abs(deltaY) > Math.abs(deltaX)) { // whether switch depends on x-axis scroll more than y-axic scroll
        return
      }
      if (!this.touch.moved) {
        this.touch.moved = true
      }
      // translate3d
      const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
      // page left-scroll distance
      const offsetWidth = Math.min(
        0,
        Math.max(-window.innerWidth, left + deltaX)
      )
      // lyricList: Vue component, $el access dom
      // lyricListdom$el
      this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
      // ,
      this.touch.percent = Math.abs(offsetWidth / window.innerWidth)
      // 
      this.$refs.lyricList.$el.style[transitionDuration] = 0
      // 
      this.$refs.middleL.style.opacity = 1 - this.touch.percent
      // CD
      this.$refs.middleL.style[transitionDuration] = 0
    },
    middleTouchEnd() {
      if (!this.touch.moved) {
        return
      }
      // cd
      let offsetWidth
      let opacity
      if (this.currentShow === 'cd') {
        // CD10
        // slide <-
        if (this.touch.percent > 0.1) {
          offsetWidth = -window.innerWidth
          opacity = 0
          this.currentShow = 'lyric'
        } else {
          offsetWidth = 0
          opacity = 1
        }
      } 
      // 10
      else {
        // slide ->
        if (this.touch.percent < 0.9) {
          offsetWidth = 0
          this.currentShow = 'cd'
          opacity = 1
        } else {
          offsetWidth = -window.innerWidth
          opacity = 0
        }
      }
      const time = 300
      this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
      // moveduration0,end300
      this.$refs.lyricList.$el.style[transitionDuration] = `${time}ms`
      this.$refs.middleL.style.opacity = opacity
      // CDmoveduration0,end300
      this.$refs.middleL.style[transitionDuration] = `${time}ms`
      this.touch.initiated = false
    },
  // CDCD
    _getPosAndScale() {
      const targetWidth = 40 // Small record width
      const paddingLeft = 40 // Small record paddingLeftCD
      const paddingBottom = 30 // Small record paddingBottomCD
      const paddingTop = 80 // Big record paddingTop
      const width = window.innerWidth * 0.8 // Big record width
      const scale = targetWidth / width /**/
      // CD
      const x = -(window.innerWidth / 2 - paddingLeft) // Fourth quadrant, Big record middle point (x, y)
      // CD
      const y = window.innerHeight - paddingTop - width / 2 - paddingBottom
      return { x, y, scale }
    }
  },
  watch: {
    currentSong(newSong, oldSong) { // currentSong change, invoked play()
      if(!newSong.id){
        return 
      }
      if (newSong.id === oldSong.id) { // mixin.js/changeMode(), currentSong.id no change currentSong no change
        return
      }
    // 
    // this.$nextTick(() => {
    //     this.$refs.audio.play()//playsrcdomready
    //     this.getLyric()
    //   })
    // $nextTicksetTimeout
    clearTimeout(this.timer)
    this.timer = setTimeout(() => {
        this.$refs.audio.play()//playsrcdomready
        this.getLyric()
      }, 1000)
      // if (!newSong.id) { // no song
      //   return
      // }
      //  
      if (this.currentLyric) {
        this.currentLyric.stop() // stop timer of the first song
  //       this.currentTime = 0
  //       this.playingLyric = ''
  //       this.currentLineNum = 0
      }
  //     clearTimeout(this.timer)
  //     this.timer = setTimeout(() => {
  //       this.$refs.audio.play() // method & data it's sync
  //       this.getLyric()
  //     }, 1000)
    },
  // playingplayingaudio
    playing(newPlaying) {
      const audio = this.$refs.audio
      this.$nextTick(() => { // method & data it's sync
        newPlaying ? audio.play() : audio.pause()
      })
    },
    // fullScreen(newVal) {
    //   if (newVal) {
    //     setTimeout(() => {
    //       this.$refs.lyricList.refresh()
    //     }, 20)
    //   }
    // }
  }
}
</script>

<style scoped lang="scss" rel="stylesheet/scss">
@import './player.scss';
</style>



