<template>
  <!-- ![min player list interface](https://i.loli.net/2019/04/10/5cad9ce9ebab7.png) -->
  <transition name="list-fade">
    <!-- 
       -->
      <!-- .playlist: -->
    <div
      class="playlist"
      v-show="showFlag"
      @click="hide"
    >
     <!-- list-wrapper: -->
      <div class="list-wrapper" @click.stop>
        <!-- list-headerheader -->
        <div class="list-header">
          <h1 class="title">
             <!-- :class="iconMode" @click="changeMode" -->
             <!-- iconModechangeModemixin -->
            <i class="icon" :class="iconMode" @click="changeMode"></i>
            <!-- {{modeText}} -->
            <span class="text">{{modeText}}</span>
            <span class="clear" @click="showConfirm"><i class="icon-clear"></i></span>
          </h1>
        </div>
        <!-- list-content -->
        <scroll class="list-content" ref="listContent" :data="sequenceList" :refreshDelay="refreshDelay">
          <!-- :refreshDelay="refreshDelay"scroll120ms -->
          <transition-group ref="list" name="list" tag="ul">
            <li
              class="item"
              v-for="(item, index) in sequenceList"
              :key="item.id"
              @click="selectItem(item,index)"
              ref="listItem"
            >
             <!-- .current -->
              <i class="current" :class="getCurrentIcon(item)"></i>
              <span class="text">{{item.name}}</span>
              <!-- mixin -->
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <!-- list-operate:: -->
        <div class="list-operate">
           <!-- @click="addSong" -->
          <div class="add" @click="addSong">
            <i class="icon-add"></i>
            <span class="text" @click="addSong"></span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span></span>
        </div>
      </div>
       <confirm
        ref="confirm"
        text=""
        confirmBtnText=""
        @confirm="confirmClear"
      >
      
      </confirm>
      <!--  -->
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
// mixinmapActions ,mapGetters,
import { mapMutations, mapActions } from 'vuex'
// mixinplayMode
import { playMode } from 'common/js/config'
import { playerMixin } from 'common/js/mixin'
import Scroll from 'base/scroll/scroll'
// 
import Confirm from 'base/confirm/confirm'
// 
import AddSong from 'components/add-song/add-song'

export default {
  components: {
    Scroll,
    AddSong,
    Confirm
  },
  mixins: [playerMixin],
  data() {
    return {
      showFlag: false,
      // scroll120ms
      refreshDelay: 120
    }
  },
  computed: {
    // ...mapGetters(['sequenceList', 'currentSong','mode', 'playlist']),
    // head
    modeText() {
      return this.mode === playMode.sequence
        ? ''
        : this.mode === playMode.random
          ? ''
          : ''
    }
  },
  methods: {
    // , ''
    ...mapActions(['deleteSong', 'deleteSongList']),
    ...mapMutations({
      setCurrentIndex: 'SET_CURRENT_INDEX',
      setPlayingState: 'SET_PLAYING_STATE',
    }),
    // playlist
    show() {
      this.showFlag = true
      // 20scroll
      setTimeout(() => {
        this.$refs.listContent.refresh()
        this.scrollToCurrent(this.currentSong)
      }, 20)
    },
    hide() {
      this.showFlag = false
    },
    showConfirm() {
      this.$refs.confirm.show()
    },
    confirmClear() {
      this.deleteSongList()
      this.hide()
    },
  // 
    getCurrentIcon(item) {
      if (this.currentSong.id === item.id) {
        return 'icon-play'
      }
      return ''
    },
    // 
    selectItem(item, index) {
      // console.log(this.playlist);
      // console.log(this.sequenceList);
      // sequenceListplayList,
      // playList[index]
      if (this.mode === playMode.random) {
        index = this.playlist.findIndex(song => {
          return song.id === item.id
        })
      }
      this.setCurrentIndex(index)
      // true
      this.setPlayingState(true)
    },
    // 
    scrollToCurrent(current) {
      // sequenceList
      const index = this.sequenceList.findIndex(song => {
        return current.id === song.id
      }) 
      // 
      // this.$refs.listContent.scrollToElement(this.$refs.list.$el.children[index], 300)
      this.$refs.listContent.scrollToElement(this.$refs.listItem[index])
    },
    // 
    deleteOne(item) {
      this.deleteSong(item)
      if (!this.playlist.length) {
        this.hide()
      }
    },
    // 
    addSong() {
      this.$refs.addSong.show()
    }
  },
  watch: {
    currentSong(newSong, oldSong) {
      if (!this.showFlag || newSong.id === oldSong.id) {
        return
      }
      // setTimeout(() => {
        this.scrollToCurrent(newSong)
      // }, 20)
    }
  }
}
</script>

<style scoped lang="scss" rel="stylesheet/scss">
@import './playlist.scss';
</style>



